@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "PT Mono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  background-color: #ffffff;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1460px;
  margin: 0 auto;
}

.overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  cursor: pointer;
}

.stop-scrolling {
  overflow: hidden;
}

.show-mobile,
.show-tablet {
  display: none;
}

@media (max-width: 1023px) {
  .show-desktop {
    display: none;
  }

  .show-tablet {
    display: block;
  }
}
@media (max-width: 767px) {
  .show-tablet {
    display: none;
  }

  .show-mobile {
    display: block;
  }
}
.header {
  background-color: #1b2d37;
  background-image: url("../../img/header-background-desktop1x.jpg");
  background-size: cover;
  min-height: 878px;
  background-repeat: no-repeat;
  background-position: center;
  color: #ffffff;
}

.header__wrapper {
  display: flex;
  flex-direction: column;
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 33px;
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  padding-left: 22px;
  padding-right: 27px;
}

.header__topLogo {
  margin-right: auto;
}

.header__contact {
  margin-right: 50px;
  min-width: 170px;
}
.header__contact:hover, .header__contact:focus {
  color: #00b2ff;
  fill: #ffffff;
  outline: none;
  transition: 0.2s ease;
}

.header__top-button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #fff;
  min-width: 180px;
  height: 45px;
  border: 1px solid #fff;
  box-sizing: border-box;
  border-radius: 5px;
  text-align: center;
  padding: 14px;
  background-color: transparent;
  cursor: pointer;
}

.header__top-button:focus,
.header__top-button:hover {
  color: #00b2ff;
  border-color: #00b2ff;
  background-color: transparent;
  outline: 0;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}

.promo {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 230px 20px 20px;
}

.promo__title {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  margin: 0;
}

.promo__text {
  max-width: 750px;
  font-family: "PT Mono", "Arial", monospace;
  font-size: 22px;
  line-height: 34px;
  padding-left: 98px;
  position: relative;
  margin-top: 27px;
  text-transform: lowercase;
}

.promo__text::before {
  content: "";
  position: absolute;
  width: 66px;
  height: 2px;
  background-color: #fff;
  top: 16px;
  left: 3px;
}

.promo__button {
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  color: #fff;
  width: 406px;
  height: 56px;
  left: 347px;
  top: 548px;
  background: #00b2ff;
  border-radius: 5px;
  margin-left: 98px;
  margin-top: 22px;
  padding: 18px;
}

.promo__button:focus,
.promo__button:hover {
  background: #fff;
  color: #00b2ff;
  fill: #fff;
  outline: 0;
  transition: 0.2s ease;
}

@media (max-width: 1023px) {
  .header {
    min-height: 0px;
    background-image: url(../../img/header-background-tablet1x.jpg);
  }

  .header__top {
    margin: 16px 0px 19px;
  }

  .header__contact {
    min-width: 24px;
    margin-right: 0;
    margin-left: 33px;
  }

  .header__contact svg {
    height: 24px;
    width: 25px;
  }

  .promo {
    min-height: 595px;
    padding: 95px 88px;
  }

  .promo__title {
    max-width: 470px;
    font-size: 60px;
    line-height: 70px;
  }

  .promo__text {
    max-width: 470px;
    font-size: 18px;
    line-height: 29px;
    padding-left: 50px;
    margin-top: 30px;
  }

  .promo__text::before {
    width: 33px;
    height: 2px;
    left: 8px;
  }

  .promo__button {
    font-size: 16px;
    line-height: 19px;
    width: 379px;
    margin-left: 51px;
    margin-top: 35px;
  }
}
@media (max-width: 767px) {
  .header {
    background-image: url(../../img/header-background-mobile1x.jpg);
  }

  .header__logo-image {
    width: 38px;
    height: 38px;
  }

  .header__contact {
    margin-left: 23px;
    padding: 0px 15px 0px 17px;
  }

  .header__top {
    margin-bottom: 9px;
  }

  .promo {
    min-height: 580px;
    padding: 220px 17px 5px calc(15px + 50% * 0.2 - 32px);
  }

  .promo__title {
    width: 280px;
    font-size: 40px;
    line-height: 47px;
    letter-spacing: 0.07em;
  }

  .promo__text {
    font-size: 16px;
    line-height: 25px;
    margin-top: 26px;
  }

  .promo__button {
    width: 286px;
    margin-left: 4px;
    margin-top: 23px;
    padding: 18px 0;
  }
}
.products {
  color: #fff;
  background: #1b2b37;
}

.products__wrapper {
  max-width: 1230px;
  padding: 23px 30px 99px;
}

.features__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1220px;
  padding: 25px;
  list-style-type: none;
  margin: 85px auto 0;
}

.products__title {
  padding-right: 5px;
  margin: 65px 0 50px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px;
}

.products__list {
  min-width: 100%;
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin: 0 -30px 0 0;
  overflow: hidden;
}

.products li {
  width: calc((100% - 90px) / 3);
  height: 50vw;
  max-height: 620px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  color: #fff;
  position: relative;
  background-repeat: no-repeat;
  background-size: 100%;
  margin-bottom: 30px;
  margin-right: 30px;
  background-color: #27333d;
}

.products li:focus,
.products li:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  outline: 0;
  transition: 0.2s ease;
  color: #00b2ff;
}

.products li::before {
  content: "";
  background-color: #00b2ff;
  background-image: url(../../img/vect.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: absolute;
  width: 63px;
  height: 63px;
  right: 0;
  bottom: 0;
  cursor: pointer;
}

.products__link {
  display: block;
  width: 100%;
  height: 100%;
  color: #fff;
  padding: 52px 45% 53px 12%;
}

.products__link:focus,
.products__link:hover {
  color: #00b2ff;
}

.products__item-develop {
  background-image: url(../../img/products-dev.png);
}

.products__item-assembling {
  background-image: url(../../img/products-as.png);
}

.products__item-components {
  background-image: url(../../img/products-comp.png);
}

@media (max-width: 1023px) {
  .products__wrapper {
    padding: 50px 25px 33px;
    max-width: 775px;
  }

  .products__title {
    font-size: 32px;
    line-height: 45px;
    max-width: 630px;
    margin: 0 0 48px;
  }

  .products__list {
    flex-direction: column;
    margin: 0 auto;
  }

  .products li {
    width: 100%;
    height: 256px;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 46px;
    margin-right: 0;
  }

  .products__item-develop {
    background-image: url(../../img/products-dev-tablet.png);
  }

  .products__item-assembling {
    background-image: url(../../img/products-as-tablet.png);
  }

  .products__item-components {
    background-image: url(../../img/products-comp-tablet.png);
  }

  .products__link {
    padding: 42px 510px 38px 45px;
  }
}
@media (max-width: 767px) {
  .products__wrapper {
    padding: 49px 15px 35px;
  }

  .products__title {
    font-size: 28px;
    line-height: 40px;
    padding-left: 9px;
    margin-bottom: 40px;
  }

  .products li {
    margin-bottom: 30px;
  }

  .products__item-develop {
    background-image: url(../../img/products-dev-mobile.png);
  }

  .products__item-assembling {
    background-image: url(../../img/products-as-mobile.png);
  }

  .products__item-components {
    background-image: url(../../img/products-comp-mobile.png);
  }

  .products__link {
    padding: 20px 90px 0 36px;
  }
}
.features {
  color: #666;
  background-image: url(../../img/features-triangles.svg);
  background-repeat: no-repeat;
  background-position: center -120px;
  margin-bottom: 93px;
}

.features__list {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1220px;
  padding: 25px;
  list-style-type: none;
  margin-top: 85px;
}

.features__list-item {
  min-height: 300px;
  border: 1px solid #ebebeb;
  width: calc(50% - 15px);
  margin-bottom: 30px;
  padding: 40px 44px;
  position: relative;
  background-color: #ffffff;
  fill: #00b2ff;
}
.features__list-item:hover, .features__list-item:focus {
  cursor: pointer;
  background-color: #00b2ff;
  border-color: #00b2ff;
  fill: #fff;
  outline: 0;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}
.features__list-item:hover h3,
.features__list-item:hover p, .features__list-item:focus h3,
.features__list-item:focus p {
  color: #ffffff;
}

.features__list-item::before {
  content: "";
  background-color: #00b2ff;
  position: absolute;
  width: 3px;
  min-height: calc(100% + 2px);
  left: -1px;
  top: -1px;
}

.features__list-title {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
  color: #00b2ff;
  margin: 15px 0;
}

.features__list-text {
  font-family: "PT Mono", "Arial", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #666;
  margin-bottom: 0;
}

.features__company {
  box-sizing: border-box;
  max-width: 1220px;
  padding: 48px 30px 24px 205px;
  margin: 0 auto;
  background-image: url(../img/2005.png);
  background-repeat: no-repeat;
  background-position: -5px 52px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.features__company-title {
  margin: 0 0 20px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 140.62%;
  color: #666666;
}

.features__company-text {
  font-family: "PT Mono", "Arial", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin: 0 0 20px;
  max-width: 100%;
}

.features__company-button {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #00b2ff;
  min-width: 180px;
  height: 45px;
  border: 1px solid #00b2ff;
  border-radius: 5px;
  text-align: center;
  padding: 14px;
  background-color: transparent;
  cursor: pointer;
  margin-top: 20px;
}

.features__company-button:focus,
.features__company-button:hover {
  color: #fff;
  border-color: #fff;
  background-color: #00b2ff;
  outline: 0;
  transition: 0.2s ease;
}

@media (max-width: 1023px) {
  .features {
    margin-bottom: 15px;
  }

  .features__list {
    margin: 25px auto 0;
  }

  .features__list-item {
    min-height: 232px;
    padding: 25px 19px 18px 27px;
  }

  .features__list-title {
    margin-top: 5px;
    line-height: 24px;
  }

  .features__company {
    padding-top: 1px;
    padding-left: 210px;
    padding-bottom: 74px;
    background-size: 251px;
    background-position: -8px 0;
  }

  .features__company-title {
    font-style: normal;
    font-weight: normal;
    font-size: 32px;
    line-height: 140.62%;
    padding-bottom: 11px;
  }
}
@media (max-width: 767px) {
  .features__wrapper {
    padding: 0px 0 31px;
  }

  .features__list {
    padding: 15px;
  }

  .features__list-item {
    width: 100%;
    min-height: 232px;
    margin-bottom: 20px;
    padding: 23px 13px 23px 25px;
  }

  .features__list-title {
    font-size: 20px;
    line-height: 28px;
    margin: 9px 0;
  }

  .features__list-text {
    margin-top: 9px;
  }

  .features__company {
    background: 0 0;
    padding: 21px 13px;
  }

  .features__company-title {
    font-size: 28px;
    line-height: 40px;
    margin-bottom: -5px;
  }

  .features__company-text {
    margin-top: 19px;
  }

  .features__company-button {
    margin-top: 8px;
  }
}
.message {
  color: #fff;
  min-height: 500px;
  background-color: #00b2ff;
  background-image: url(../../img/form-background.png), url(../../img/form-triangles.svg);
  background-repeat: no-repeat;
  background-position: calc(50% - 430px) center, calc(50% + 779px) 34px;
}

.message__wrapper {
  max-width: 1230px;
  padding: 90px 30px 113px;
}

.message__form {
  max-width: 570px;
  margin-left: auto;
}

.message__form fieldset {
  border: 0;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.message__form legend {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  padding: 0;
  margin-bottom: 10px;
  margin-left: -5px;
  color: #fff;
}

.message__form-text {
  font-family: "PT Mono", "Arial", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 50px;
}

.message__form-row {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.message__form input,
.message__form textarea {
  border: 0;
  font-size: 16px;
  background-color: rgba(255, 255, 255, 0.1);
  min-width: 270px;
  height: 50px;
  padding: 14px 5px 14px 17px;
  margin-bottom: 40px;
}

.message__form input::placeholder,
.message__form textarea::placeholder {
  color: #fff;
}

.message__form textarea {
  min-width: 100%;
  padding: 13px 20px 13px 16px;
  height: 154px;
  margin-bottom: 45px;
}

.message__form-button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #fff;
  min-width: 270px;
  height: 45px;
  border: 1px solid #fff;
  box-sizing: border-box;
  border-radius: 5px;
  text-align: center;
  background-color: transparent;
  cursor: pointer;
  padding: 0;
  margin: 0;
}

.message__form label {
  width: 260px;
  font-family: "PT Mono", "Arial", monospace;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-transform: none;
  padding-left: 32px;
  margin-top: 9px;
  position: relative;
  cursor: pointer;
}

.message__form label::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  border: 1px solid #fff;
  box-sizing: border-box;
  left: 0;
  top: 0;
}

.message__form input[type=checkbox]:checked + label::after {
  content: "";
  position: absolute;
  width: 6px;
  height: 3px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  left: 5px;
  top: 8px;
  transform: translateY(-80%) rotate(-45deg);
}

@media (max-width: 1023px) {
  .message {
    background-image: none;
  }

  .message__wrapper {
    padding: 80px 28px 104px 33px;
  }

  .message__form {
    max-width: 590px;
    margin: 0 auto;
  }

  .message__form legend {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 15px;
  }

  .message__form span {
    margin-bottom: 55px;
  }

  .message__form textarea {
    height: 159px;
    margin-bottom: 48px;
  }

  .message__form-button {
    min-width: 280px;
  }

  .message__form label {
    margin-right: 10px;
  }
}
@media (max-width: 767px) {
  .message__wrapper {
    padding: 15px 15px 43px;
  }

  .message__form {
    padding-top: 14px;
  }

  .message__form legend {
    max-width: 250px;
    font-size: 28px;
    line-height: 40px;
    margin-left: 0;
    margin-bottom: 10px;
  }

  .message__form span {
    margin-bottom: 25px;
  }

  .message__form-row {
    flex-direction: column;
  }

  .message__form input {
    margin-bottom: 32px;
  }

  .message__form textarea {
    height: 104px;
    margin-bottom: 33px;
  }

  .message__form-row {
    flex-direction: column;
  }

  .message__form label {
    margin-top: 31px;
  }
}
.footer {
  background-color: #fafafa;
  min-height: 316px;
}

.footer__wrapper {
  display: grid;
  grid-template-columns: min-content 1fr 1fr;
  grid-template-rows: 1fr min-content;
}

.footer__links {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 2;
  min-width: 270px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 130px;
  padding: 45px 0 0px 21px;
}

.footer__social {
  padding: 0;
  margin: 52px 0 0;
  display: flex;
  padding: 0;
  margin: 0;
  list-style: none;
}

.footer__social li {
  padding-right: 15px;
}

.footer__logo-image {
  padding-top: 20px;
  margin-bottom: 45px;
}

.footer__section_nav {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 3;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-right: auto;
  max-width: 525px;
  box-sizing: border-box;
  padding: 45px 0px 0px 25px;
  display: flex;
}

.footer__section_nav label {
  padding-left: 23px;
}

.footer__section_nav ul {
  padding: 0 0 0 20px;
  margin-right: auto;
  list-style: none;
  font-size: 16px;
}

.footer__section-contacts a,
.footer__section-contacts p,
.footer__section_nav a,
.footer__section_nav p {
  color: #8d969b;
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  color: #1b2d37;
  opacity: 0.5;
}

.footer__section-contacts {
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column-start: 3;
