  .footer label {
    box-sizing: border-box;
    margin: 0;
    padding: 28px 5px 5px 5px;
    cursor: pointer;
  }
  .footer label::before, .footer label::after {
    content: "";
    position: absolute;
    width: 16px;
    height: 1px;
    background: #1b2b37;
    opacity: 0.5;
    right: 7px;
    top: 36px;
    display: block;
  }
  .footer label::after {
    transform: rotate(90deg);
  }
  .footer input:checked ~ label {
    padding-bottom: 25px;
  }
  .footer input:checked ~ label::after {
    display: none;
  }
  .footer input ~ ul {
    display: none;
  }
  .footer input[id=nav]:checked ~ ul {
    display: block;
  }
  .footer input[id=contacts]:checked ~ .footer__contacts-list {
    display: flex;
  }

  .footer__info {
    margin-top: 0;
    padding: 29px 29px 4px;
    flex-direction: column;
    align-items: center;
  }

  .footer__info-studio {
    margin-left: 0;
  }
