.modal {
  box-sizing: border-box;
  display: none;
  position: fixed;
  color: #ffffff;
  width: 490px;
  height: 605px;
  padding: 28px 40px;
  background-color: #00b2ff;
  box-shadow: 0 20px 40px 0 rgba(41, 52, 73, 0.75);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}
.modal legend {
  font-size: 28px;
  line-height: 40px;
  padding-left: 5px;
  margin-top: 0;
}
.modal span {
  padding-right: 10px;
  margin-bottom: 30px;
}
.modal input {
  min-width: 100%;
  height: 51.83px;
  margin-bottom: 30.45px;
}
.modal textarea {
  height: 104px;
  margin-bottom: 32px;
}
.modal button[type=submit] {
  width: 100%;
  min-height: 47px;
  margin-bottom: 29px;
}
.modal label {
  width: 330px;
  padding-left: 47px;
  margin-top: 5px;
}

.form input::-webkit-input-placeholder,
.form textarea::-webkit-input-placeholder {
  font-family: "PT Mono", "Arial", monospace;
  font-size: 16px;
  line-height: 22px;
  font-weight: normal;
}

.modal-show {
  display: block;
}

.modal-error {
  animation: error 0.3s;
}

.modal__button-close {
  position: absolute;
  width: 22px;
  height: 22px;
  font-size: 0;
  background-color: transparent;
  border: none;
  padding: 0;
  right: 18px;
  top: 18px;
  cursor: pointer;
}
.modal__button-close::before, .modal__button-close::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 1px;
  background-color: #ffffff;
  top: 10px;
  left: -1px;
  border-radius: 5px;
}
.modal__button-close::before {
  transform: rotate(45deg);
}
.modal__button-close::after {
  transform: rotate(-45deg);
}
.modal__button-close:focus, .modal__button-close:active {
  outline: none;
}

.form fieldset {
  border: 0;
  padding: 0;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
}

