  content: "";
  position: absolute;
  width: 6px;
  height: 3px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
  left: 5px;
  top: 8px;
  transform: translateY(-80%) rotate(-45deg);
}

